// RWD 圖片
img {
    max-width: 100%; //使圖片自適應覆元素大小
    height: auto;
    vertical-align: middle; //去除圖片下方2~3px空白
    object-fit: cover; //維持原比例
}

a {
    display: block;
    text-decoration: none;
}

h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
    margin-bottom: 0;    
    line-height: 1.5;
}

ul {
    list-style: none;
}

ol, ul, dl {    
    margin-bottom: 0;
}

ol, ul {
    padding-left: 0;
}

p {
    margin-bottom: 0;
}

hr {
    margin: 0;
    color: #fff;
    border: 0;
    opacity: 1;
}